import React from 'react';
import { connect } from 'react-redux';
import {searchRecipe, submitSearch, searchTermGiven} from '../actions';
//
class SearchBar extends React.Component {
	render(){
		return (
			<div className="item">
		      <div className="ui transparent icon input">
		        <input type="text" placeholder="Search..."
		        onChange={e=> this.props.searchRecipe(e.target.value)}
				onKeyDown={e=> {
				     if(e.keyCode === 13){
				      	this.props.submitSearch(this.props.searchTerm.Recipe)
				      	this.props.searchTermGiven(this.props.searchTerm.Recipe)
				     }
				}} style={{color: 'snow'}}/>
		        <i 
		        onClick={e=> {
				      	this.props.submitSearch(this.props.searchTerm.Recipe)
				      	this.props.searchTermGiven(this.props.searchTerm.Recipe)
				    }
				}
		        className="inverted search link icon"></i>
		      </div>
		    </div>
		);
	}
}
const mapStateToProps = (state) => {
	return {searchTerm: state.search}
}
export default connect(mapStateToProps,{searchRecipe, submitSearch, searchTermGiven})(SearchBar);

