import React from 'react';
import {connect } from 'react-redux';
import RecipeCard from './RecipeCard';
import { bookmark } from '../actions';
import SearchBookmarks from './SearchBookmarks';
class Bookmark extends React.Component{
	renderList(){
		const {bookmarks, filtered} = this.props;
		if(!bookmarks.length){
			return (
				<h2 class="ui center aligned icon header">
				  <i class="bookmark outline icon"></i>
				  <div class="content">
				    Bookmarks
				    <div class="sub header">Look up recipes and come back here when you have some bookmarked.</div>
				  </div>
			</h2>
			);
		}
		if(!filtered.length){
			return bookmarks.map(info => {
				return (
					<RecipeCard info={info} bookmark={this.props.bookmark} key={info.recipe.label}/>
				);
			})
		}
		else{
			return filtered.map(info => {
				return (
					<RecipeCard info={info} key={info.recipe.label}/>
				);
			})
		}
	}
	renderSearch(){
		const {bookmarks} = this.props;
		if(!bookmarks.length){
			return null;
		}
		else{
			return <SearchBookmarks/>;
		}
	}
	render(){
	return (
		<div>
			{this.renderSearch()}
			{this.renderList()}
		</div>
	);
	};
};
const mapStateToProps = state => {
	return {bookmarks: state.bookmark,
		filtered: state.filteredSaved
	};
}
export default connect(mapStateToProps, {bookmark})(Bookmark);