import React from 'react';
import { connect } from 'react-redux';
import { Pagination} from 'semantic-ui-react';

import Accordion from './Accordion';
import RecipeCard from './RecipeCard';
import { handlePage, submitFilter, bookmark } from '../actions';

class MealFilter extends React.Component {
	renderCount(){
		if(!this.props.recipes){
			return null;
		}
		const {count} = this.props.recipes;
		const finalPage = Math.round(count/10);
		return (
			<div>
				<p>{count} recipes found</p>
				<Pagination defaultActivePage={1} totalPages={finalPage} 
				onPageChange={(e,data)=>{
					this.props.handlePage(data.activePage, finalPage);
				}}/>
			</div>
		);
	}
	renderList(){
		if(!this.props.recipes){
			return (
				<div className="ui blue message">
				  <p>Enter food name to look up recipes</p>
				</div>
			);
		}
		const {hits} = this.props.recipes;
		return hits.map(info => {
			return (
				<RecipeCard info={info} key={info.recipe.label}/>
			);
		})
	}
	render(){
		return (
			<div className="ui container">
				<div className="ui stackable grid">
					<div className="four wide column">
						<Accordion/>
					</div>
					<div className="twelve wide column">
						{this.renderCount()}
						{this.renderList()}
					</div>
				</div>
			</div>
		);
	}
}
const mapStateToProps = state => {
	return {recipes: state.search.Recipes}
}
export default connect(mapStateToProps,{handlePage, submitFilter, bookmark})(MealFilter);