import React from 'react';
import { Form, Button, Select} from 'semantic-ui-react';
import { dietOptions, healthOptions, ingrOptions } from '../data/dietLabels';
import { connect } from 'react-redux';
import {searchTermGiven, submitFilter } from '../actions';
class Accordion extends React.Component {
	state = {calories: 300, submitted: false}
 	renderSearchError = () => {
 		const {searchTerm} = this.props;
 		const {submitted} = this.state;
 		if(submitted && !searchTerm.available){
 			return (
				<div className="ui fluid red message">
				  <p>Pls enter a food to search before filtering</p>
				</div>
			);
 		}
 		if(submitted && searchTerm.available && !searchTerm.success){
 			return (
				<div className="ui fluid red message">
				  <p>No results found for such filter</p>
				</div>
			);
 		}
 		return null;
 	}
 	handleChange = (e, {name, value}) => {
 		this.setState({[name]:value})
 		if(this.state.submitted){
 			this.setState({submitted: false})
 		}
 	}
 	handleSubmit = (e,values) => {
 		this.setState({submitted: true})
 		if(this.props.searchTerm.available){
 			this.props.submitFilter(this.state)
 		}
	}
	render(){
		const {handleChange} = this;
		const{calories} =this.state;
		return(
			<div>
				<Form onSubmit={this.handleSubmit}>
				     <Form.Input
				     	name="ingrParams"
				        control={Select}
				        options={ingrOptions}
				        fluid
				        label={{ children: 'Max no. of Ingredients', htmlFor: 'form-select-control-ingredients' }}
				        placeholder='Max no. of ingredients'
				        search
				        searchInput={{ id: 'form-select-control-ingredients' }}
				        onChange={handleChange}
				     />
				     <Form.Input
				     	name="dietParams"
				        control={Select}
				        options={dietOptions}
				        fluid selection
				        label={{ children: 'Diet', htmlFor: 'form-select-control-diet' }}
				        placeholder='Diet Options'
				        search
				        searchInput={{ id: 'form-select-control-diet' }}
				        onChange={handleChange}
				     />
				     <Form.Input
				     	name="healthParams"
				        control={Select}
				        options={healthOptions}
				        fluid multiple selection
				        label={{ children: 'Health', htmlFor: 'form-select-control-health' }}
				        placeholder='Health Options'
				        search
				        searchInput={{ id: 'form-select-control-health' }}
				        onChange={handleChange}
				     />
					<Form.Input
			            label={`Calories: ${calories}cal `}
			            min={100}
			            max={2000}
			            name='calories'
			            onChange={handleChange}
			            step={100}
			            type='range'
			            value={calories}
			          />
				    <Form.Field
				      id='form-button-control-public'
				      control={Button}
				      content='Filter'
				      label='Filter Results'
				    />
				</Form>
				{this.renderSearchError()}
			</div>
		);
	}
}
const mapStateToProps = state => {
	return {searchTerm: state.search
	}
}
export default connect(mapStateToProps,{searchTermGiven, submitFilter})(Accordion);

