import React from 'react';
import { Tab } from 'semantic-ui-react';
import { connect } from 'react-redux';
import { bookmark } from '../actions';
const panes = [
  { menuItem: 'Ingredients', render: (props) => {
  	return (
  	<Tab.Pane attached={false}>
  		<div className="ui items">
				<div className="item">
					<div className="image">
						 <img src={props.info.recipe.image} alt="Food"/>
					</div>
					    <div className="content" style={{overflowY: 'scroll', height: '220px'}}>
					      <a href={props.info.recipe.url}className="header" target="_blank" rel="noopener noreferrer">{props.info.recipe.label}</a>
					    <div className="meta">
					    	{props.renButton()}
					        <span>{props.info.recipe.ingredientLines.map(line=> {
					        	return (
					        		<p>{line}</p>
					        	);
					        })}</span>
					    </div>
					    </div>
				</div>
			</div>
  	</Tab.Pane>) }
  },
  { menuItem: 'Nutrients', render: (props) => {
  		return (
  		<Tab.Pane attached={false}>
  			<div className="ui items">
					<div className="item">
						<div className="image">
						   <img src={props.info.recipe.image} alt="Food"/>
						</div>
	  			<div className="content" style={{overflowY: 'scroll', height: '220px'}}>
					<a href={props.info.recipe.url}className="header" target="_blank" rel="noopener noreferrer">{props.info.recipe.label}</a>
					    <div className="meta">
					 		{props.renButton()}
					       <span>{Object.keys(props.info.recipe.totalNutrients).map(key=>{
					       		return (
					       			<p>{`${props.info.recipe.totalNutrients[key].label}: ${Math.round(props.info.recipe.totalNutrients[key].quantity)} ${props.info.recipe.totalNutrients[key].unit}`}</p>
					       		);
					       })}</span>
					    </div>
				</div>
				</div>
			</div>
  		</Tab.Pane>
  		)
  	}
 }
]

class RecipeCard extends React.Component {
	constructor(props){
		super(props);
		this.state = {
			bgColor: "",
			icon: "",
			text: ""
		}
	}
	cardMarked = () =>{
		const {bookmarked} = this.props.info;
		if(bookmarked){
			this.setState({
				bgColor: "pink",
				icon: "bookmark",
				text: "Bookmarked"
			})
		} else{
			this.setState({
				bgColor: "instagram",
				icon: "bookmark outline",
				text: "Bookmark"
			})
		}
	}
	componentDidMount(){
		this.cardMarked();
	}
	renderButton = ()=>{
		const {info, bookmark} = this.props;
		const {bgColor, icon, text} = this.state;
		return(
			<button class={`ui ${bgColor} button`} style={{margin: '5px 0'}} onClick={() => {
				bookmark(info)
				this.cardMarked()
			}
			}>
				<i class={`${icon} icon`}></i>
					{text}
			</button>
			);
	}
	render(){
		const {info} = this.props;
		return (
			<Tab menu={{ secondary: true, pointing: true }} panes={panes} info={info} key={info.recipe.label} renButton={this.renderButton}/>
		);
	};
};

export default connect(null, {bookmark})(RecipeCard);