import React from 'react';
import { connect } from 'react-redux';
import { Router, Route } from 'react-router-dom';
import Homepage from './Homepage';
import MealFilter from './MealFilter';
import history from '../history';
import SideMenu from './SideMenu';
import Bookmark from './Bookmark';
class App extends React.Component {
	render(){
		return (
			<div className="ui container">
			{//when you pass down a history object to browserrouter, it ignores the history prop, hence we create our own plain router
			}
				<Router history={history}>
					<div>
					<SideMenu/>
						<Route path="/mealtime" exact component={Homepage}/>
						<Route path="/search" exact component={MealFilter}/>
						<Route path="/bookmarks" exact component={Bookmark}/>
					</div>
				</Router>
			</div>
		);
	}
}
const mapStateToProps = state => {
	return {recipes: state.search.Recipes}
}
export default connect(mapStateToProps,{})(App);