import React from 'react';
import { connect } from 'react-redux';
import {searchBookmarks} from '../actions';
//
class SearchBookmarks extends React.Component {
	render(){
		return (
			<div className="inverted item">
		      <div className="ui transparent icon input">
		        <input type="text" placeholder="Search Bookmarks..."
		        onChange={e=> this.props.searchBookmarks(e.target.value)}
				onKeyDown={e=> {
				     if(e.keyCode === 13){
				      	this.props.searchBookmarks(e.target.value);
				     }
				}} style={{color: 'black'}}/>
		        <i 
		        onClick={e=> {
				      	this.props.searchBookmarks(e.target.value);
				    }
				}
		        className="inverted search link icon"></i>
		      </div>
		    </div>
		);
	}
}
const mapStateToProps = (state) => {
	return {searchTerm: state.search}
}
export default connect(mapStateToProps, {searchBookmarks})(SearchBookmarks);