import history from '../history';

const key = 'a3068a044c623318afaa747afbf29331';
const id = 'eb17d189';
export const searchRecipe = value => {
	return {
		type: 'SEARCH',
		payload: value
	}
}
export const searchTermGiven = value => {
	return {
		type: 'SEARCH_GIVEN',
		payload: Boolean(value)
	}
}
export const submitSearch = value => async dispatch => {
	const response = await fetch(`https://api.edamam.com/search?q=${value}&app_id=${id}&app_key=${key}`)
	const obj = await response.json();
	let query = `&q=${value}`;
	dispatch({type: 'HANDLE_QUERY', payload: query}); 
	dispatch({type: 'SUBMIT_SEARCH', payload: obj});
	history.push('/search');
}
export const handlePage = (page, finalPage) => async (dispatch,getState) => {
	let {query} = getState().search;
	const origQuery = query;
	const start = (page - 1) * 10;
	if(page !==finalPage){
		const end = page * 10;
		query = `${query}&from=${start}&to=${end}`;
	}else{
		query = `${query}&from=${start}`;
	}
	try{
		const res = await fetch(`https://api.edamam.com/search?app_id=${id}&app_key=${key}` + query);
		const obj = await res.json();
		dispatch({type: 'HANDLE_PAGE', payload: obj});
	}catch(err){
		dispatch({type: 'SUBMIT_FAIL', payload: origQuery || ""})
	} 
}

export const submitFilter = formValues => async (dispatch,getState) => {
	let {query} = getState().search;
	const origQuery = query;
	const {ingrParams, dietParams, healthParams, calories} = formValues;
	if(ingrParams){
		query = query +"&ingr="+ ingrParams;
	}
	if(dietParams){
		query = query + "&diet=" + dietParams;
	}
	if(healthParams){
		healthParams.forEach(param => {
			query = query + "&health=" + param;
		})
	}
	if(calories){
		query = query + "&calories=" + calories + "%2B";
	}
	//dispatch query into state object before attaching the number of queries
	//so we can manipulate it in handle page action
	dispatch({type: 'HANDLE_QUERY', payload: query})
	//attached starting query here
	query = query + '&from=0'
	try{
	const response = await fetch(`https://api.edamam.com/search?app_id=${id}&app_key=${key}` + query)
	const obj = await response.json();
	dispatch({type: 'SUBMIT_PARAMS', payload: obj});
	}catch(err){
		dispatch({type: 'SUBMIT_FAIL', payload: origQuery || ""})
	} 
}
