export const bookmark = (info) => (dispatch,getState)=>{
	//checks if there is any bookmarks
	if(getState().bookmark.length){
		//checks for existing bookmarks
		const exists = getState().bookmark.filter(data=>{
			return data.recipe.label === info.recipe.label;
		})
		if(exists.length){
			info.bookmarked = false;
			dispatch({type: 'EXISTS', payload: info.recipe.label});
			return;
		}
	}
	info.bookmarked = true;
	dispatch({type: 'BOOKMARK', payload: info});
}
export const searchBookmarks = label => (dispatch, getState) =>{
	const savedList = getState().bookmark;
	let filteredList = [];
	if(label.length){
		filteredList = savedList.filter(info=>{
			return info.recipe.label.toLowerCase().search(label.toLowerCase()) !== -1;
		})
	}
	dispatch({type: 'FILTER_SAVED', payload: filteredList})
}